            <p class="font-courier my-0"><span class="res-progress" title="Edit">?/?</span></p>
          </div>
        </div>
        <div class="prg-legend-wrapper text-center my-5">
          <span class="prg-legend-area">
            <i class="fa fa-circle mr-1" style="color: rgb(12, 206, 107)"></i> 100
            <i class="fa fa-circle mr-1 ml-4" style="color: rgb(83, 116, 250)"></i> 81 - 99
            <i class="fa fa-circle mr-1 ml-4" style="color: rgb(255, 164, 0)"></i> 41 - 80
            <i class="fa fa-circle mr-1 ml-4" style="color: rgb(255, 105, 105)"></i> 1 - 40
            <i class="fa fa-circle mr-1 ml-4" style="color: rgb(158, 158, 158)"></i> 0
